@import "common/styles/mixins";
.header {
   position: fixed;
   top: 0;
   left: 0;
   width: 100%;
   height: 60px;
   background: #fcfcfc;
   box-shadow: 0px 2px 10px rgba(109, 109, 109, 0.25), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
   z-index: 1002;
   &__container {
      @include flexWithHeight(row, space-between, center, nowrap, 100%);
   }
   &__logo-wrapper {
      position: relative;
      &:hover {
         .header__logo {
            transform: scale(1.03);
         }
         .header__hat {
            transform: scale(1.03);
         }
      }
   }
   &__logo {
      position: relative;
      z-index: 1;
   }

   &__hat {
      width: 30px;
      height: 30px;
      position: absolute;
      right: -12px;
      top: -2px;
      opacity: 0.8;
      transform: rotate(7deg);
   }
   &__link {
      display: inline-block;
      position: relative;
      background: #366eff;
      box-shadow: 0px 4px 18px rgba(54, 110, 255, 0.35), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
      border-radius: 30px;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      text-align: center;
      letter-spacing: 0.01em;
      color: #ffffff;
      text-transform: capitalize;
      padding: 8px 28px;
      overflow: hidden;
      span {
         position: relative;
         z-index: 1;
      }
      &::after {
         content: "";
         position: absolute;
         left: 50%;
         top: 50%;
         transform: translate(-50%, -50%);
         width: 0;
         height: 0;
         background-color: #fcfcfc34;
         border-radius: 50%;
         transition: all ease-in 0.15s;
      }
      &:active {
         top: 1px;
         &::after {
            width: 100%;
            height: 100%;
            border-radius: 0;
         }
      }
   }
   &__auth {
      display: flex;
      align-items: center;
   }
   &__link-users {
      margin-right: 25px;
      padding-top: 5px;
      &:hover {
         svg {
            fill: #1976d2;
         }
      }
   }
}
